import React from "react"
import Markdown from "markdown-to-jsx"
import Modal from "./Modal"
import * as codeStyles from "../styles/code.module.css"
//import ElementTree from "./ElementTree"

export default function EditReg(props) {
    return (
        <Modal open={props.open}>
            <div className={codeStyles.codeWrapper}>
                <div className={codeStyles.btnDiv}>
                    <button onClick={() => props.handleOpen(false)} className={codeStyles.closeBtn}>X CLOSE</button>
                </div>
                <div className={codeStyles.codeDiv}>
                    {props.data.length > 0 && props.data.map((item, i) => <Markdown children={item.text} key={i}  /> )} 
                     {/* {props.data.length > 0 && React.createElement("div", null, props.data[0].code)}  */}
                </div>
            </div>
        </Modal>
    )
}
