

export default function normalizeObject(obj) {
    var result = [];
    if (Array.isArray(obj)) {
        for (let i of obj) {
            result.push(normalizeObject(i));
        }
    } else if (typeof obj == "object") {
        for (let i of Object.keys(obj)) {
            result.push({ name: i, value: normalizeObject(obj[i]) });
        }
    } else {
        return obj;
    }
    return result;
}