import React from 'react'
import Layout from "../components/Layout"
import { graphql } from 'gatsby'
import { remark } from "remark";
import normalizeObject from "../js/normalizeObj"
import parseMd from "../js/parseMd"
import Description from "../components/Description";
import Hint from "../components/Hint"
import Seed from "../components/Seed";
import ButtonBar from "../components/ButtonBar";
import EditReg from "../components/EditReg"
import * as lessonStyles from "../styles/lesson.module.css"


export default function Lesson({ data }) {

  const [desc, setDesc] = React.useState("")
  const [hint, setHint] = React.useState("")
  const [seeder, setSeeder] = React.useState("")
  const [editable, setEditable] = React.useState("")
  const [hintModalOpen, setHintModal] = React.useState(false)
  const [isProj, setIsProj] = React.useState(false)
  const [projectModalOpen, setProjectModal] = React.useState(false)
  const [isEditReg, setEditReg] = React.useState(false)
  const [isERegOpen, setERegOpen] = React.useState(false)

  React.useEffect(() => {
    let markdown = data.markdownRemark.rawMarkdownBody
    remark()
      .use(() => tree => {
        let data = parseMd(tree.children)
        setDesc(data.description)
        setHint(data.hints)
        setSeeder(data.seed)
        data.seed.length > 0 && setIsProj(true)
        setEditable(data.editReg)
        data.editReg.length > 0 && data.editReg !== "\n\n" && setEditReg(true)
      })
      .processSync(normalizeObject(markdown))
  }, [data.markdownRemark.rawMarkdownBody])

  return (
    <Layout>
      <ButtonBar setHintModal={setHintModal} setProjectModal={setProjectModal} isEditReg={isEditReg} setERegModal={setERegOpen} isProj={isProj} />
      <div className={lessonStyles.lessonWrapper}>
        <h2 className={lessonStyles.lessonTitle}>{data.markdownRemark.frontmatter.title}</h2>
        <Description data={desc} />
        <Hint data={hint} open={hintModalOpen} handleOpen={setHintModal} />
        {
          isProj && <Seed mkdStr={seeder} open={projectModalOpen} handleOpen={setProjectModal} />
        }
        {
          isEditReg && <EditReg data={editable} handleOpen={setERegOpen} open={isERegOpen} />
        }
      </div>
    </Layout>
  )
}
export const lessonQuery = graphql`
    query lessonQuery($id: String) {
      markdownRemark(frontmatter: {id: {eq: $id}}) {
        id
        frontmatter {
          challengeType
          dashedName
          title
          id
        }
        rawMarkdownBody
      }
    }
    `
