const setElementType = (entry) => {
    switch (entry.type) {
        case "text":
            return "span";
        case "paragraph":
            return "p";
        case "heading":
            switch (entry.depth) {
                case 1:
                    return 'h1';
                case 2:
                    return 'h2'
                default:
                    return 'h3'
            }
        case "code":
            return 'code'
        case "inlineCode": // what element should be returned for this type? I don't think 'code' is appropriate
            return 'em' // using 'em' for now
        default:
            return 'span'
    }
};

export default setElementType