


const germinateSeeds = (arr) => {
    let htmlData
    let cssData
    let jsData
    let newCssData
    let notSoClassyHtml
    let htmlStr
    let jStr = ""
    let mkdStr = ""
    if (arr.filter(item => item.lang === "html" && item.type === "code").length > 0) {
        htmlData = arr.filter(item => item.lang === "html" && item.type === "code")[0].value
    }
    if (arr.filter(item => item.lang === "css" && item.type === "code").length > 0) {
        cssData = arr.filter(item => item.lang === "css" && item.type === "code")[0].value
    }
    if (arr.filter(item => item.lang === "js" && item.type === "code").length > 0) {
        jsData = arr.filter(item => item.lang === "js" && item.type === "code")[0].value
    }
    if(!htmlData){
        mkdStr = ""
        return mkdStr
    }
    if (!cssData && !jsData) {
        mkdStr = htmlData.trim()
    }
    if (cssData) {
        newCssData = cssData.replace(":root", ":host")
        notSoClassyHtml = htmlData.replaceAll("class", "className")
        htmlStr = "<style>\n" + newCssData.trim() + "</style>" + notSoClassyHtml.trim()
    }
    if (cssData && !jsData) {
        mkdStr = htmlStr.trim()
    }
    if (jsData) {
        jStr = "<script defer>\n" + jsData.trim() + "\n</script>\n".trim()
        mkdStr = jStr.trim() + htmlStr.trim()
    }
    return mkdStr.trim()
}


export default germinateSeeds