import React from "react"
import * as btnBarStyles from "../styles/buttonbar.module.css"

export default function ButtonBar({ setHintModal, setProjectModal, setERegModal, isEditReg, isProj }) {
    return (
        <header className={btnBarStyles.bar}>
            <button onClick={() => setHintModal(true)} className={btnBarStyles.barBtn}>HINT</button>
            {isEditReg && <button onClick={() => setERegModal(true)} className={btnBarStyles.barBtn}>CODE</button>}
            {isProj && <button onClick={() => setProjectModal(true)} className={btnBarStyles.barBtn}>VIEW PROJECT</button>}
        </header>
    )
}