import React from "react"
import Markdown from "markdown-to-jsx";
import ReactShadowRoot from "react-shadow-root";
import Modal from "./Modal";
import * as seedStyles from "../styles/seed.module.css"

export default function Seed(props) {
    return (
        <div>
            <Modal open={props.open}>
                <div className={seedStyles.btnDiv}>
                    <button onClick={() => props.handleOpen(false)} className={seedStyles.closeBtn}>X CLOSE</button>
                </div>
                <div>
                    <ReactShadowRoot>
                        <Markdown children={props.mkdStr}/>
                    </ReactShadowRoot>
                </div>
            </Modal>
        </div>
    );


}