import React from "react";
import setElementType from "../js/setElementType";
//import Markdown from "markdown-to-jsx"

const ElementTree = (props) => {
    return Array.from(props.data).map(
        (item, i) =>
            React.createElement(setElementType(item), { key: i }, item.children.map((entry, y) => React.createElement(setElementType(entry), { key: y }, entry.value)))
    )
    // return Array.from(props.data).map(
    //     (item, i) => !item.children ? <Markdown chidlren={item} key={i} /> : item.children.map((child, y) => <Markdown children={child.value} key={y} />)
    // )
}

export default ElementTree

