import React from 'react'
import Modal from './Modal';
import ElementTree from './ElementTree';
import * as hintStyles from "../styles/hint.module.css"

export default function Hint(props) {
 
    return (
        <Modal open={props.open}>
            <div className={hintStyles.hintWrapper}>
                <div className={hintStyles.btnDiv}>
                    <button onClick={() => props.handleOpen(false)} className={hintStyles.closeBtn}>X CLOSE</button>
                </div>
                <div className={hintStyles.hintDiv}>
                    <ElementTree data={props.data} />
                </div>
            </div>
        </Modal>
    );
}