import parseCodeStr from "./parseCodeStr"
import germinateSeeds from "./germinateSeeds"

const parseMd = (data) => {
    let description = []
    let hints = []
    let seed = []
    let editableRegion = []
    const endDesc = data.filter(item => item.type === "heading" && item.children[0].value === "--description--")[0].position.end.line
    const startHint = data.filter(item => item.type === "heading" && item.children[0].value === "--hints--")[0].position.start.line
    const endHint = data.filter(item => item.type === "heading" && item.children[0].value === "--hints--")[0].position.end.line
    const startSeed = data.filter(item => item.type === "heading" && item.children[0].value === "--seed--")[0].position.start.line
    // const endSeed = data.filter(item => item.type === "heading" && item.children[0].value === "--seed--")[0].position.end.line
    const startSeedContents = data.filter(item => item.type === "heading" && item.children[0].value === "--seed-contents--")[0].position.start.line

    data.forEach(item => {
        if (item.children && item.position.start.line > endDesc && item.position.end.line < startHint) {
            description.push(item)
        }
        if (item.children && item.position.start.line > endHint && item.position.end.line < startSeed) {
            hints.push(item)
        }
        if (item.position.start.line > startSeedContents) {
            parseCodeStr(item, editableRegion)
            seed.push(item)
        }
    })

    let obj = {}
    obj.description = description
    obj.hints = hints
    obj.seed = germinateSeeds(seed)
    obj.editReg = editableRegion
    return obj
}



export default parseMd