


const parseCodeStr = (item, arr) => {
    let headReg = /<\/head>/
    let tailReg = /<\/html>/
    let seedReg = /--fcc-editable-region--/
    if (headReg.test(item.value)) {
        let splitHead = item.value.split(headReg)
        let cutOffTail = splitHead[1].split(tailReg)
        item.value = cutOffTail[0]
    }
    if (seedReg.test(item.value)) {
        let splitCode = item.value.split(seedReg)
        let editable = {}
        let codeStr = ""
        editable.text = "```" + item.lang + item.value + "```"
        arr.push(editable)
        codeStr += splitCode[0] + splitCode[2]
        item.value = codeStr
    }

    return item
}


export default parseCodeStr